import * as React from "react";
import "./../../style/Components/Arrow.less";

export interface State {
  textLabel: string;
  textValue: string;
}

export class Arrow extends React.Component<any, any> {
  public state = {
    textLabel: "",
    textValue: "",
  };

  // private static updateCallback: (data: object) => void = null;

  constructor(props: any) {
    super(props);
    console.log("Component Constructor");
    console.log(props);
    this.state = {
      textLabel: "",
      textValue: "",
    };
  }

  update = (newState: State) => {
    console.log("Component Update " + newState);
    this.setState({ newState });
  };

  render() {
    return (
      <>
        <div className="arrow-rectangle-box">
          <div className="arrow-rectangle-content">
            <div>
              <p>{this.props.data[1]}</p>
              <p>{this.props.data[0]}</p>
            </div>
          </div>
        </div>
        <div className="triangle-right"></div>
      </>
    );
  }
}

export default Arrow;
